import "./App.css";
import { useState, useRef } from "react";
import dayjs from "dayjs";
import "dayjs/locale/ko";
dayjs.locale("ko");

function App() {
  const [listName, setListName] = useState([
    "남자 코트 추천",
    "하남 맛집 추천",
    "겨울 간식 추천",
  ]);
  const makeLikes = () => {
    return listName.map((el) => 0);
  };
  const [like, setLike] = useState(makeLikes);
  const [modal, setModal] = useState(false);
  const [choice, setChoice] = useState(0);
  const [inputValue, setInputValue] = useState("");
  const [validInputValue, setValidInputValue] = useState(true);

  const listInputRef = useRef(null);

  const recodingDate = dayjs().format("YY-MM-DD");
  console.log("recodingDate", typeof recodingDate);

  const handleLike = (index) => {
    const copy = [...like];
    copy[index] = copy[index] + 1;
    setLike(copy);
  };

  const handleChangeListName = () => {
    let copy = [...listName];
    copy[0] = "여자 코트 추천";
    setListName(copy);
  };

  const sortList = () => {
    const newListName = [...listName];
    newListName.sort();
    setListName(newListName);
  };

  const toggleModal = () => {
    setModal(!modal);
  };

  const addList = (e) => {
    console.log("타겟값", listInputRef.current.value);
    if (listInputRef.current.value.length < 1) {
      setValidInputValue(false);
      return;
    }
    setValidInputValue(true);
    console.log("왜 안됨");
    const newListName = [...listName];
    newListName.unshift(inputValue);
    const newLike = [...like];
    newLike.unshift(0);
    setListName(newListName);
    setLike(newLike);
    listInputRef.current.value = "";
  };

  const delList = (i) => {
    const newListName = [...listName];
    newListName.splice(i, 1);
    setListName(newListName);
    const newLike = [...like];
    newLike.splice(i, 1);
    setLike(newLike);
  };

  const Modal = ({ title, handleChangeListName }) => {
    return (
      <div className="modal">
        <h4>{title[choice]}</h4>
        <p>날짜</p>
        <p>상세내용</p>
        <button onClick={handleChangeListName}>글수정</button>
      </div>
    );
  };

  console.log("listName", listName);
  console.log("like", like);
  console.log("validInputValue", validInputValue);

  return (
    <div className="App">
      <div className="black-nav">
        <h4>React Blog</h4>
      </div>
      <button onClick={sortList}>가나다순정렬</button>
      <button onClick={handleChangeListName}>글제목변경</button>

      {listName.map((name, i) => {
        return (
          <div className="list" key={i}>
            <h4
              onClick={() => {
                toggleModal();
                setChoice(i);
              }}
            >
              {listName[i]}
              <span
                onClick={(e) => {
                  e.stopPropagation();
                  handleLike(i);
                }}
              >
                ❤
              </span>{" "}
              {like[i]}{" "}
            </h4>
            <p>{recodingDate}</p>
            <button onClick={() => delList(i)}>글삭제</button>
          </div>
        );
      })}

      <input
        type="text"
        ref={listInputRef}
        onChange={(e) => {
          setInputValue(e.target.value);
        }}
      />
      <button
        onClick={(e) => {
          addList(e);
        }}
      >
        글추가
      </button>
      {validInputValue ? null : (
        <span>한 글자 이상 입력해야만 글추가가 가능합니다</span>
      )}
      {modal ? (
        <Modal title={listName} handleChangeListName={handleChangeListName} />
      ) : null}
    </div>
  );
}

export default App;
